import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ShareModule } from '../share/share.module'
import { AdminComponent } from './admin.component';
import { AdminRoutingModule } from './admin-routing.module';
import { CardComponent } from './home_page/card/card.component';
import { FooterComponent } from './layout/footer/footer.component'
import { HeaderComponent } from './layout/header/header.component'
import { SlidenavComponent } from './layout/slidenav/slidenav.component'
import { CardsComponent } from './home_page/card_list/cards.component'
import { LoadingComponent } from '../component/loading/loading.component'
import { LoginService } from '../login/login.service'
import { AuthLogin } from '../auth-login.service'
import { childPanelComponent } from './home_page/card/chilld-panel.component'
@NgModule({
  imports: [
    ShareModule,
    AdminRoutingModule
  ],
  declarations: [
    AdminComponent, 
    CardComponent, 
    CardsComponent, 
    HeaderComponent, 
    FooterComponent, 
    SlidenavComponent, 
    LoadingComponent,
    childPanelComponent
  ],
  providers: [LoginService, AuthLogin]
})
export class AdminModule { }



