import { Component, OnInit, Input } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ITEM } from '../card.model'
import { fadeInAnimation, bounceInAnimation, bounceInUpAnimation } from '../../animation'
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { childPanelComponent } from '../card/chilld-panel.component'
import { AppService } from '../../../app.service'
@Component({
  selector: 'app-main-cards',
  templateUrl: './cards.component.html',
  animations: [fadeInAnimation, bounceInAnimation, bounceInUpAnimation],
  styleUrls: ['./cards.component.styl']
})
export class CardsComponent implements OnInit {
  queryData: any = {};
  loading: boolean = true;
  list: ITEM[];
  constructor(public dialog: MatDialog, private http: HttpClient, private appService: AppService) {}

  ngOnInit(): void {
    this.http.get('/cards/list').subscribe(data => {
        this.list = data['list']
    })
    this.http.get(`${JSON.parse(localStorage.getItem('env'))['url']}/sgp/MonitorService/all`).subscribe(data => {
      this.queryData = data
      this.loading = false
    })
  } 
}



